<template>
  <div class="dialog-wrapper" v-if="show">
    <div class="box-wrapper">
      <p class="title">{{ title }}</p>
      <van-field v-model="value" placeholder="请输入" />
      <div class="btn-wrapper">
        <van-button round type="primary" @click="hiddenDialog">取消</van-button>
        <van-button round type="info" @click="confirm">确认</van-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DialogComponent",
  data() {
    return {
      title: "",
      show: false,
      value: "",
      callback:undefined
    };
  },
  methods: {
    showDialog: function (title, content,callback) {
      this.title = title;
      this.value = content || "";
      this.callback = callback;
      this.show = true;
    },
    confirm: function () {
     if (this.callback) {
      this.callback(this.value);      
     }
     this.hiddenDialog();
    },
    hiddenDialog: function () {
      this.show = false;
    },
  },
};
</script>

