<template>
  <div class="list-wrapper">
    <input ref="copyInput" type="hidden" v-model="cardNo" />
    <div class="sku-wrapper">
      <van-button type="info" @click="getCardNo">生成卡号</van-button>
      <span class="cardNo">{{ cardNo }}</span>
      <van-button type="primary" @click="copyClick">复制</van-button>
    </div>
    <div
      class="item"
      @click="itemClick()"
      v-for="(item, index) in listData"
      :key="index"
    >
      <div class="title-wrapper">
        <span class="title">{{ item.title }}</span>
        <div class="btn-wrapper">
          <div class="button" @click="updateItem(item)">修改</div>
          <div class="button" @click="addContent(item)">新增</div>
          <div class="button" @click="deleteItem(item)">删除</div>
        </div>
      </div>
      <div class="content-wrapper">
        <div
          class="content-item"
          v-for="(contentItem, inx) in item.contents"
          :key="inx"
        >
          <p class="content">{{ inx + 1 }}) {{ contentItem.content }}</p>
          <div class="item-btn-wrapper">
            <van-button type="info" @click="editContent(contentItem)"
            >编辑</van-button
          >
            <van-button type="info" @click="deleteContent(item,contentItem)"
            >删除</van-button
          >
          </div>
          
        </div>
      </div>
    </div>
    <div class="bottom">
      <van-button type="info" @click="addItem">新增</van-button>
    </div>
    <dialog-component ref="dialog"/>
  </div>
</template>

<script>
import DialogComponent from './DialogComponent.vue'
export default {
  name: "Server",
  components:{DialogComponent},
  data() {
    return {
      cardNo: "",
      listData: [],
      show:false
    };
  },
  created() {
    this.getListDatas();
  },
  methods: {
    showPopup:function(){
      this.show = true
    },
    //获取所有内容
    getListDatas: function () {
      this.bmobUtil.getListDatas().then(res =>{
         this.listData = res;
      });
    },
    addItem: function () {
      let that = this;
      this.$refs.dialog.showDialog('增加标题','',function(value){
        that.addItemData(value);
      })
    },
    addItemData: function (title, objectId, contentIds) {
      this.bmobUtil.addItemData(title, objectId, contentIds).then(() =>{
         this.getListDatas();
      });
    },
    deleteItem: function (titleItem) {
      this.bmobUtil.deleteObject("item", titleItem.objectId).then(() =>{
         this.getListDatas();
      });
    },
    updateItem: function (titleItem) {
      let that = this;
      this.$refs.dialog.showDialog('修改标题',titleItem.title,function(value){
        that.addItemData(value, titleItem.objectId);
      })
    },
    addContent: function (titleItem) {
      let that = this;
      this.$refs.dialog.showDialog('新增内容','',function(value){
        that.addContentData(value, titleItem);
      }) 
    },
    addContentData: function (content, titleItem) {
      this.bmobUtil.addContentData(content, titleItem).then(() =>{
         this.getListDatas();
      });
    },
    deleteContent: function (titleItem,contentItem) {
      this.bmobUtil.deleteObject("content", contentItem.objectId).then(() =>{
          let contentIds = [];
          if (titleItem.contentIds && titleItem.contentIds.length != 0) {
            contentIds = titleItem.contentIds.split(",");
          }
          let index = contentIds.indexOf(contentItem.objectId);
          delete contentIds[index];
          this.addItemData(undefined,titleItem.objectId,contentIds.join(','));
      });
    },
    editContent:function(contentItem){
      let that = this;
      this.$refs.dialog.showDialog('编辑内容',contentItem.content,function(value){
        that.bmobUtil.addContentData(value, undefined,contentItem.objectId)
        .then(()=>{
          that.getListDatas();
        })
      }) 
    },
    itemClick: function () {},
    getCardNo: function () {
      let that = this;
      this.bmobUtil.getCardNo(cardNo =>{
         that.cardNo = cardNo;
      })
    },
    copyClick: function () {
      this.$refs.copyInput.type = "text";
      this.$refs.copyInput.focus();
      this.$refs.copyInput.select();
      document.execCommand("copy");
      this.$refs.copyInput.type = "hidden";
    },
    randomNum: function (minNum, maxNum) {
      return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
    },
  },
};
</script>

